// 判断是否登录，重定向
import { Redirect, Route } from "react-router-dom";
import { notification } from "antd";

function PrivateRoute({ children, ...rest }) {
    // 提取token
    let token = sessionStorage.token;
    return (
        <Route { ...rest } render={
            ({ location }) => {
                if (token) {
                    return children;
                } else {
                    //错误提示框
                    notification.error({
                        message: `错误：401`,
                        description: 'token过期，请重新登陆账户！',
                    });
                    return (
                        <Redirect
                            to={ {
                                pathname: "/login",
                                state: { from: location }
                            } }
                        />
                    )
                }
            }
        }/>
    )
}

export default PrivateRoute;